import uniIconfont from '@/components/uni-iconfont/uni-icon.vue'
import bugBtn from '@/components/uni-bug-btn/uni-bug-btn.vue';
import {turnPage} from '@/common/global.js';
export default {
	name: 'GoodList',
	components: {
		uniIconfont,
		bugBtn
	},
	data () {
		return {
			list: this.value
		}
	},
	props: {
		// 是否需要勾选框
		nselect: {
			type: Boolean,
			default: false
		},
		// 最小化显示
		small: {
			type: Boolean,
			default: false
		},
		value: {
			type: Array,
			default: function () {
				return [];
			}
		}
	},
	watch: {
		value (val) {
			this.list = val;
		}
	},
	onLoad() {},
	methods: {
		// 选中商品
		clkChoose (e, data) {
			if (e.error) {
				uni.showToast({title: '无法购买更多', icon: 'none', position: 'bottom'});
			} else {
				// 勾选中商品
				this.$set(data, 'select', true);
				this.$store.commit('addGoodToCar', data);
				// 数量变化监听
				this.$emit('changeCount', data);
				this.$emit('input', this.list);
			}
		},
		clkLine (data) {
			this.$emit('click', data);
		},
		clkSelect (data) {
			this.$set(data, 'select', !data.select);
			this.$emit('input', this.list);
		}
	}
}
